import React from "react"
import glamorous from 'glamorous'

const WrapperDiv = glamorous.span({
    position: `relative`,
    borderBottom: `1px dotted black`,
    color: `blue`,
})

const TooltipDiv = glamorous.span(
    {
        visibility: `hidden`,
        width: `120px`,
        backgroundColor: `black`,
        color: `#fff`,
        textAlign: `center`,
        padding: `5px 0`,
        borderRadius: `6px`,
        position: `absolute`,
        zIndex: `1`,
    },
    (props) => ({
        visibility: props.tooltipVisible ? `visible` : `hidden`,
    })
)

export default class Tooltip extends React.Component {
  static defaultProps = {
    text: 'placeholder',
  }

  state = {
    tooltipVisible: false,
  }

  handleClick = () => {
    this.setState(state => {
      return {
        tooltipVisible: state.tooltipVisible ? false : true,
      }
    })
  }

  render() {
    return (
        <WrapperDiv onClick={this.handleClick}>
            {this.props.children}<TooltipDiv tooltipVisible={this.state.tooltipVisible}>{this.props.text}</TooltipDiv>
        </WrapperDiv>
    )
  }
}



